.relogioNumero {
	background-color: #5d677c;
	box-shadow: 2px 2px 4px #2b2b2b inset;
	height: 3.6rem;
	width: 3rem;
	padding: 18px 14px;
	border-radius: 10px;

	@media screen and (min-width: 1280px) {
		height: 10.8rem;
		width: 9rem;
	}
}

.relogioDivisao {
	height: 4.2rem;

	@media screen and (min-width: 1280px) {
		height: 12.6rem;
	}
}
